* {
  -webkit-transition: background 0.1s ease-in, color 0.1s linear;
  transition: background 0.1s ease-in, color 0.1s linear;
}
.flex-center-h,
.flex-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.flex-center-h {
  flex-direction: row;
}

.flex-center .flex-start {
  align-self: flex-start;
}

h1,
h2,
h3,
h4 {
  font-weight: 300;
}

h3,
h4 {
  margin: 0;
}

label {
  font-family: inherit;
  font-weight: 300;
  font-size: 1.2rem;
}

button,
input[type="button"],
input[type="submit"] {
  border: 0;
  background: rgb(196, 195, 195);
  color: rgb(51, 51, 51);
  box-shadow: 1px 1px 1px #8a8a8a;
  padding: 1em;
  border-radius: 2px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  outline: 0;
}

button.selected,
input[type="button"].selected,
input[type="submit"].selected {
  background: rgb(82, 82, 82);
  color: rgb(196, 195, 195);
}

button:disabled,
input[type="button"]:disabled,
input[type="submit"]:disabled {
  opacity: 0.7;
  cursor: default;
}

button:hover:not(:disabled),
input[type="button"]:hover:not(:disabled),
input[type="submit"]:hover:not(:disabled),
.black-btn {
  background: rgb(51, 51, 51);
  color: rgb(196, 195, 195);
}

.black-btn {
  background: rgb(51, 51, 51) !important;
  color: rgb(196, 195, 195) !important;
}
.black-btn:hover {
  background: rgb(26, 106, 211) !important;
  color: white !important;
}

input[type="text"],
textarea {
  width: 15em;
  background: rgb(51, 51, 51);
  color: rgb(196, 195, 195);
  border: 0;
  padding: 0.7em;
  outline: 0;
  border-radius: 5px;
}

select {
  background: rgb(179, 178, 178);
  color: rgb(51, 51, 51);
  padding: 0.7rem;
  border-radius: 3px;
  outline: 0;
  cursor: pointer;
}

span.info {
  position: relative;
}

span.info::after {
  content: "i";
  display: inline-block;
  width: 1.3em;
  height: 1.3em;
  text-align: center;
  font-family: cursive;
  background: rgb(51, 51, 51);
  border-radius: 50%;
  color: rgb(196, 195, 195);
  line-height: 1.3em;
  margin: 1em;
  position: absolute;
  top: -0.2em;
}
span.info > span:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0.2em;
  left: -1.2em;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-right: 10px solid rgb(196, 195, 195, 0.9);
}
span.info > span {
  display: none;
  position: absolute;
  top: 0em;
  left: 3em;
  padding: 1em;
  width: 20em;
  text-align: center;
  background: rgb(196, 195, 195, 0.9);
  color: rgb(75, 74, 74);
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgb(109, 109, 109);
}
span.info:hover > span {
  display: block;
}

.form-table {
  display: table;
}
.form-table > .form-row {
  display: table-row;
}
.form-table > .form-row > * {
  margin: 1em 0;
  display: table-cell !important;
  vertical-align: middle;
}

.form-row > label {
  margin: 0 !important;
  margin-right: 10px !important;
}

.form-row.submit-row {
  display: flex;
  justify-content: flex-end;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
}
.cards-container > .card-item {
  background: rgb(196, 195, 195);
  flex-basis: 15rem;
  padding: 1em;
  border-radius: 3px;
  margin: 1em;
  height: 15rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.card-item > .card-heading {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  margin-top: 1rem;
}

.card-item > .card-desc {
  font-style: italic;
  color: rgba(51, 51, 51, 0.8);
  margin-top: 0.5em;
}

.card-item > .card-special {
  color: rgba(0, 0, 0, 0.6);
  align-self: center;
  position: absolute;
  bottom: 5rem;
  font-style: italic;
}
.card-item > .card-head-special {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: rgba(0, 0, 0, 0.6);
  font-style: italic;
}

.card-item > .card-btns {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
}

.card-btns > button {
  background: rgb(51, 51, 51);
  color: rgb(196, 195, 195, 0.9);
}
.card-btns > button:hover {
  background: rgb(92, 92, 92);
}

.sq-list {
  display: flex;
  flex-wrap: wrap;
}
.sq-list-item {
  background: rgb(196, 195, 195);
  color: rgb(51, 51, 51);
  width: 12rem;
  height: 12rem;
  border-radius: 3px;
  margin: 1rem;
  padding: 1rem;
  opacity: 0.8;
  cursor: pointer;
}
.sq-list-item:hover {
  background: rgb(179, 178, 178);
  color: rgb(48, 48, 48);
  opacity: 1;
  box-shadow: 0px 0px 1px 1px #e3e3e3;
}

.block-list {
  display: flex;
  flex-wrap: wrap;
}
.block-list > .block {
  margin: 1em;
  padding: 1em;
  border-radius: 2px;
  background: rgb(179, 178, 178);
  color: rgb(51, 51, 51);
}

.block > .block-txt {
  display: flex;
  flex-direction: column;
}
.block-txt > span {
  margin: 5px 0px;
}

.block > .block-btns {
  margin-top: 1rem;
}

.block-btns > button {
  background: rgba(51, 51, 51, 0.8);
  color: rgb(179, 178, 178);
  margin: 0 0.1em;
}

.warning {
  margin: 1rem 0;
  padding: 1rem;
  background: rgb(167, 114, 16);
  border-radius: 5px;
}

.color-btn {
  display: inline-block;
  width: 1.4em;
  height: 1.4em;
  opacity: 0.8;
  border-radius: 50%;
  border: 0;
  border: 2px solid rgba(40, 120, 255, 0.5);
  margin-right: 5px;
  cursor: pointer;
}

.color-btn:hover,
.color-btn.selected {
  border: 2px solid rgba(0, 0, 0, 0.8);
  opacity: 1;
}

.elebtn {
  opacity: 0.5;
}
.elebtn.selected {
  opacity: 1;
}

.table-list {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.table-list > table {
  min-width: 500px;
}
.table-list > table > thead {
  background: rgb(51, 51, 51);
  border-radius: 2px;
}
.table-list > table > thead > tr > td {
  padding: 1rem;
  color: white;
  border-radius: 2px;
}

.table-list > table > tbody > tr:nth-of-type(1n) {
  background: rgb(255, 255, 255);
}
.table-list > table > tbody > tr:nth-of-type(2n) {
  background: rgb(209, 209, 209);
}
.table-list > table > tbody > tr > td {
  padding: 0.7rem;
}

.table-list > table > tbody > tr > td > .btn-container {
  display: flex;
  justify-content: center;
}

.welcome-dialogue {
  margin-top: 1rem;
}
.big-label {
  font-size: 1.4rem;
}
.big-input {
  width: 20rem !important;
  font-size: 1.2rem;
}
.big-desc {
  width: 20rem;
  font-size: 1.2rem;
  display: block;
  height: 6rem;
}
.big-btn {
  width: 21.5rem;
}

.screen {
  display: flex;
  flex-direction: column;
  position: relative;
}

.screen .screen-table {
  margin: 1rem 0;
}

