.row-block,
.col-block,
.main-block {
  background: #727272;
  color: rgb(235, 235, 235);
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  padding: 0.8rem;
  border-radius: 3px;
  cursor: pointer;
  border-bottom: 1px solid #e3e3e3;
  border-right: 1px solid #e3e3e3;
}

.row-block:hover,
.col-block:hover,
.main-block:hover,
.table-footer td:hover,
.table-header td:hover {
  background: #525252;
  color: rgb(250, 250, 250);
}

.table-block {
  padding: 1rem;
  font-family: "Roboto", sans-serif;
  position: relative;
  border-right: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  background: #b9b9b9;
  border-radius: 3px;
  cursor: pointer;
  color: rgb(51, 51, 51);
}

.table-block:hover {
  background: #727272;
  color: rgb(235, 235, 235);
}

.empty-block {
  min-width: 2rem;
  padding: 0.6rem;
}

.table-sidebar td div {
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  min-height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.table-sidebar td div span {
  min-width: -webkit-max-content;
  min-width: -moz-max-content;
  min-width: max-content;
  display: block;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.table-header td,
.table-footer td {
  background: #727272;
  color: rgb(235, 235, 235);
  padding: 0.7rem;
  font-family: "Raleway", sans-serif;
  text-align: center;
  font-size: 16px;
  border-radius: 3px;
  cursor: pointer;
}

.main-block > span,
.table-block > span,
.row-block > span,
.col-block > span {
  display: block;
  margin: 0.3em 0;
  text-align: center;
}

.table-block > button {
  width: 2.3rem;
  height: 2.3rem;
  line-height: 0.5rem;
  box-shadow: none !important;
  background: rgb(51, 51, 51);
  color: #fff;
  display: block;
  margin: 0 auto;
}

.table-block > button:hover {
  background: rgb(17, 82, 167) !important;
  color: #fff;
}

.pdfrender .screen-table {
  margin: 0;
}
.render .empty-block > * {
  display: none;
}
.render .screen-element {
  display: none;
}
.imgrender {
  justify-content: center;
  align-items: center;
  flex-basis: 100%;
}
.imgrender .screen-table {
  width: 100%;
}

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(22, 22, 22, 0.287);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  font-family: "Roboto", sans-serif;
}

.modal-inside {
  background: #fff;
  padding: 1em;
  border-radius: 5px;
  position: relative;
}

.modal-x {
  position: absolute;
  top: 0.5em;
  right: 1em;
}

.modal-loader {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #474747;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

